.header-wallet {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 1px 40px;
}

.container-walletIcon {
  align-items: center;
  display: flex;
}

.img-walletIcon {
  margin-right: 20px;
  width: 60px;
}

h1 {
  color: #183967;
}

select {
  border: solid 1.5px;
  border-radius: 20px;
  padding: 2px 4px;
}

.div-email-total {
  display: flex;
}

.div-total {
  display: flex;
  margin-left: 30px;
}

.element-total {
  margin-left: 5px;
}

.form-wallet {
  background-color: #183967;
  display: flex;
  justify-content: space-around;
  padding: 20px 10px;
}

.form-edit {
  background-color: yellow;
  display: flex;
  justify-content: space-around;
  padding: 20px 10px;
}

input , select {
  background-color: white;
  margin-left: 5px;
}

.container-table {
  display: flex;
  flex-direction: column;
}

.tr-title {
  align-items: center;
  display: flex;
  background-color: gray;
  justify-content: space-between;
  width: 100%;
}

.tr-expense {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

th {
  border-left: solid white 1px;
  padding: 7px 0;
  text-align: center;
  width: 140px;
}

td {
  padding: 7px 0;
  text-align: center;
  width: 140px;
}

.btn-del {
  background-color: red;
  cursor: pointer;
}

.btn-edit {
  background-color: yellow;
  cursor: pointer;
  margin-right: 10px;
}

.btn-form {
  border-radius: 5px;
  color: #183967;
  cursor: pointer;
  font-weight: bold;
  padding: 4px 15px;
}

.total {
  color: #183967;
  font-weight: bold;
}
