.container-headerLogin {
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 20px;
  padding: 10px;
}

.container-center {
  display: flex;
  justify-content: center;
  margin-top: 3%;
}

.container-login {
  align-items: center;
  background-color: rgb(255, 255, 255, 0.4);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  height: 250px;
  justify-content: space-around;
  padding-bottom: 20px;
  width: 300px;
}

.container-btns-login {
  display: flex;
  justify-content: space-between;
  width: 200px;
}

input {
  border: solid 1.5px;
  border-radius: 20px;
  padding: 4px 10px;
  width: 230px;
}

.btn-enter {
  background-color: #183967;
  border-radius: 5px;
  cursor: pointer;
  padding: 4px 10px;
  width: 80px;
}

.btn-forgot {
  background-color: transparent;
  border: none;
  color: #183967;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  margin-top: 0;
}
